.form {
    margin-top: 10px;
    padding: 15px;
    display: flex;
    justify-content: center;
    background: rgb(0, 212, 255);
    background: linear-gradient(90deg, rgba(0, 212, 255, 1) 0%, rgba(4, 115, 191, 1) 100%, rgba(7, 52, 150, 1) 100%);
}

.form input {
    padding: 5px 10px;
    margin-right: 10px;
    font-size: 20px;
    border-radius: 5px;
    outline: none;
}

.form input:focus {
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .2),
        0 2px 10px 0 rgba(0, 0, 0, .5);
    ;
}

.form button {
    padding: 10px;
    font-size: 20px;
    font-weight: 700;
    color: #383838;
    border-radius: 5px;
    cursor: pointer;
    transition: .5s box-shadow;

}

.form button:hover {
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .2),
        0 2px 10px 0 rgba(0, 0, 0, .5);
    ;
}

@media (max-width:576px) {
    .form input {
        font-size: 13px;
        margin-right: 5px;
    }

    .form button {
        font-size: 13px;
    }
}