@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
.form {
    margin-top: 10px;
    padding: 15px;
    display: flex;
    justify-content: center;
    background: rgb(0, 212, 255);
    background: linear-gradient(90deg, rgba(0, 212, 255, 1) 0%, rgba(4, 115, 191, 1) 100%, rgba(7, 52, 150, 1) 100%);
}

.form input {
    padding: 5px 10px;
    margin-right: 10px;
    font-size: 20px;
    border-radius: 5px;
    outline: none;
}

.form input:focus {
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .2),
        0 2px 10px 0 rgba(0, 0, 0, .5);
    ;
}

.form button {
    padding: 10px;
    font-size: 20px;
    font-weight: 700;
    color: #383838;
    border-radius: 5px;
    cursor: pointer;
    transition: .5s box-shadow;

}

.form button:hover {
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .2),
        0 2px 10px 0 rgba(0, 0, 0, .5);
    ;
}

@media (max-width:576px) {
    .form input {
        font-size: 13px;
        margin-right: 5px;
    }

    .form button {
        font-size: 13px;
    }
}
.weather {
    padding: 0 20px;
    -webkit-animation: faded .4s;
            animation: faded .4s;
}

@-webkit-keyframes faded {
    from {
        opacity: 0.1;
    }

    to {
        opacity: 1;
    }
}

@keyframes faded {
    from {
        opacity: 0.1;
    }

    to {
        opacity: 1;
    }
}

.weather__items {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.weather__icon {
    width: 150px;
    height: 150px;
}

.weather__item {
    font-size: 30px;
    font-weight: 500;
}

.weather__item span {
    font-size: 60px;
}

.weather__list {
    padding: 0 20px;
    border-top: 2px solid rgba(128, 128, 128, .6);
    list-style-type: none;
}

.weather__list li {
    font-size: 25px;
    line-height: 38px;
}

.weather__list-line {
    padding-top: 20px;
    position: relative;
    width: 270px;
    height: 70px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    overflow: hidden;

}

.weather__list-line span {
    position: absolute;
    height: 270px;
    width: 270px;
    border: 2px solid #000;
    border-top: 2px dotted #000;
    border-right: 2px dotted #000;
    border-radius: 50%;
}

.weather__list-line span:after {
    content: '';
    position: absolute;
    height: 20px;
    width: 20px;
    border: 2px solid yellow;
    background: yellow;
    border-radius: 50%;
    box-shadow: 0 0 10px yellow;
    top: 34px;
    left: 20px;
}

.weather__list-sun {
    display: flex;
    justify-content: space-between;
}

.weater__time {
    margin-top: 5px;
    text-align: center;
    font-size: 25px;
}

@media (max-width:576px) {
    .weater__time {
        font-size: 20px;
    }

    .weather__icon {
        width: 100px;
        height: 100px;
    }

    .weather__items {
        flex-wrap: wrap;
    }

    .weather__item {
        font-size: 25px;
    }

    .weather__item span {
        font-size: 50px;
    }

    .weather__list li {
        font-size: 20px;
        line-height: 30px;
    }
}
.app {
    padding: 20px 0;
    width: 600px;
    height: 732px;
    border-radius: 4px;
    background-color: rgba(225, 255, 255, .5);
    box-shadow: 3px 3px 10px rgba(0, 0, 0, .5);
}

.app h1 {
    text-align: center;
    font-size: 40px;
    font-weight: 700;
    text-transform: uppercase;
}

.app h2 {
    margin-top: 20px;
    font-size: 30px;
    text-align: center;
    font-weight: 500;
}

@media (max-width:768px) {
    .app {
        width: 100%;
        height: 100%;
    }
}

@media (max-width:576px) {
    .app {
        min-height: 100vh;
    }

    .app h1 {
        font-size: 35px;
    }

    .app h2 {
        font-size: 25px;
    }

}
body {
    margin: auto;
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: url(/Weather/static/media/blue_sky.3311531d.jpg)center center/cover no-repeat;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(26, 26, 26);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

* {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

@media (max-width:576px) {
    body {
        align-items: baseline;
    }
}
